.card {
  border: 10px;
  border-color: black;
  position: relative;
  width: 100%;
  /* height: 100%; */
  padding: 20px;
  max-width: 85vw;
  height: 50vh;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  background-color: white;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.card-header {
  display: flex;
  flex-direction: row;
}

.card > h3 {
  color: black;
  width: 100%;
}

.tinderCards__cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}

.swipe {
  width: 100%;
  height: 100%;
  padding-left: 5%;
  position: absolute;
}

.cut-text {
  color: black;
  width: 95%;

  text-overflow: ellipsis;
  overflow: hidden;
  /* width: 160px; */
  /* background-color: red; */
  height: 50%;
  height: 10em;
}

.e-label {
  margin: 5px;
}

.action {
  position: absolute;
  font-size: 60px;
  margin: 8px;
  padding: 10px;
  border-radius: 20px;
  opacity: 0.8;
  top: 96px;
}

.left {
  transform: rotateY(0deg) rotate(-30deg); /* needs Y at 0 deg to behave properly*/
  left: 32px;
  border: 8px solid green;
  color: green;
}

.right {
  transform: rotateY(0deg) rotate(30deg); /* needs Y at 0 deg to behave properly*/
  right: 32px;
  color: red;
  border: 8px solid red;
}


.centered-element {
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
